@import 'ScssHelpers/helpers-all.scss';

.pagination {
	display: flex;
	justify-content: center;
	align-items: center;

	@include bp($point_3) {
		display: none;
	}
}

.pagination__list {
	position: relative;

	display: flex;
	align-items: center;
	justify-content: center;

	margin: 0 -0.6rem 0 0;
}

.pagination__item {
	position: relative;

	display: flex;
	align-items: center;
	justify-content: center;

	width: 4.5rem;
	height: 4.5rem;

	padding: 0 0.6rem 0 0;

	color: $black_2;

	font-weight: 500;
	font-size: 2rem;
	line-height: 130%;
	letter-spacing: 0.03em;

	cursor: pointer;

	&:not(&--dots_mod) {
		&:hover,
		&:focus {
			font-weight: 900;
			text-decoration: underline;
		}
	}
}

.pagination__item--active_mod {
	font-weight: 900;
	text-decoration: underline;
}

.pagination__link {
	color: $black_2;

	&:hover,
	&:focus {
		text-decoration: none;
	}
}

.pagination__item--prev_mod,
.pagination__item--next_mod {
	&:after {
		content: '';
		position: absolute;
		top: 50%;

		width: 1.8rem;
		height: 1.8rem;

		background-image: url("data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTQiIGhlaWdodD0iMTYiIHZpZXdCb3g9IjAgMCAxNCAxNiIgZmlsbD0ibm9uZSIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj4KPHBhdGggZD0iTTEzIDE0LjA2MjJMMi41IDhMMTMgMS45Mzc4MkwxMyAxNC4wNjIyWiIgZmlsbD0iIzE0MTQxNCIgc3Ryb2tlPSIjMTQxNDE0IiBzdHJva2Utd2lkdGg9IjIiLz4KPC9zdmc+Cg==");
		background-repeat: no-repeat;

		transition: transform 0.3s ease;
	}
}

.pagination__item--next_mod {
	&:after {
		transform: translateY(-50%) scaleX(-1);
	}

	&:hover {
		&:after {
			transform: translateY(-50%) scaleX(-1) scale(1.1);
		}
	}
}

.pagination__item--prev_mod {
	&:after {
		transform: translateY(-50%) rotate(0deg);
	}

	&:hover {
		&:after {
			transform: translateY(-50%) rotate(0deg) scale(1.1);
		}
	}
}