@import 'ScssHelpers/helpers-all.scss';

.card_pagination {
	margin-top: -5.6rem;

	@include bp($point_2, min) {
		padding: 0 var(--gl-indent) 8rem;
	}

	@include bp($point_3) {
		padding: 5rem var(--gl-indent);
	}
}

.card_pagination__wrapper {
	@include bp($point_2, min) {
		padding-bottom: 5.4rem;
	}

	@include bp($point_3) {
		padding-bottom: 4rem;
	}
}

.card_pagination__list {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;

	@include bp($point_2, min) {
		margin: 0 -2rem -3.2rem 0;
	}

	@include bp($point_3) {
		margin: 0 -1.4rem -3.2rem 0;
	}
}

.card_pagination__btn {
	width: 100%;

	text-align: center;
}

.card_pagination__button {
	width: 14.4rem;

	margin: 0 auto;

	@include btn_size_normal;
	@include btn_style_white;

	@include bp($point_2, min) {
		display: none;
	}

	@include bp($point_3, $point_4) {
		display: block;
	}
}

